:root {
  --light: #faf8f8;
  --dark: #141021;
  --secondary: #284b63;
  --tertiary: #84a59d;
  --visited: #afbfc9;
  --gray: #4e4e4e;
  --lightgray: #f0f0f0;
  --outlinegray: #cacaca;
}

[saved-theme="dark"] {
  --light: #1e1e21 !important;
  --dark: #fbfffe !important;
  --secondary: #6b879a !important;
  --visited: #4a575e !important;
  --tertiary: #84a59d !important;
  --gray: #d4d4d4 !important;
  --lightgray: #292633 !important;
  --outlinegray: #444444 !important;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #faf8f8;
  background-color: var(--light);
}

pre, code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.darkmode {
  padding: 2em;
  margin: 1em 0;
  min-width: 30px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}

.darkmode > .toggle {
  display: none;
  box-sizing: border-box;
}

.darkmode svg {
  opacity: 0;
  position: absolute;
  width: 20px;
  height: 20px;
  top: calc(50% - 10px);
  margin: 0 7px;
  fill: var(--gray);
  transition: opacity 0.1s ease;
}

.toggle:checked ~ label > #dayIcon {
  opacity: 0;
}

.toggle:checked ~ label > #nightIcon {
  opacity: 1;
}

.toggle:not(:checked) ~ label > #dayIcon {
  opacity: 1;
}

.toggle:not(:checked) ~ label > #nightIcon {
  opacity: 0;
}


.ProseMirror {
  padding: 0 30vw 2em;
  font-size: 1rem;
}

.ProseMirror * {
  transition: background 0.3s ease;
}

.ProseMirror p.is-editor-empty:first-child::before {
  color: var(--outlinegray);
  content: attr(data-placeholder);
  float: left;
  height: 0;
  pointer-events: none;
}

@media screen and (max-width: 1200px) {
  .ProseMirror {
    padding: 2em 5vw;
  }
}

.ProseMirror:focus {
  outline: none;
}

/* Typography */
h1, h2, h3, h4, h5, h6, thead {
  color: var(--dark);
  font-weight: revert;
  margin: 2rem 0 0;
  padding: 2rem auto 1rem;
}

p, ul, text, a, tr, td, ol, ul {
  color: var(--gray);
  fill: var(--gray);
  font-weight: revert;
  margin: 0.8rem 0;
  padding: revert;
  line-height: 1.6rem;
}

ol, ul {
  padding-left: 1.5em;
  margin: 0;
}

ol p, ul p {
  margin: 0.2rem 0;
}

table {
  border: 1px solid var(--outlinegray);
  width: 100%;
  padding: 1.5em;
  border-collapse: collapse;
}

td, th {
  padding: 0.2em 1em;
  border: 1px solid var(--outlinegray);
}

img {
  padding: 10px;
  max-width: 100%;
  border-radius: 3px;
  margin: 1em 0;
}

sup {
  line-height: 0
}

blockquote {
  margin-left: 0em;
  border-left: 3px solid var(--secondary);
  padding-left: 1em;
  transition: border-color 0.2s ease;
}

a {
  font-size: 1em;
  color: var(--secondary);
  transition: color 0.3s ease !important;
}

a:hover {
  color: var(--tertiary);
}

pre {
  padding: 0.75em;
  border-radius: 3px;
}

code {
  font-size: 0.85em;
  padding: 0.15em 0.3em;
  border-radius: 5px;
  background: var(--outlinegray);
}

span[data-time] {
  padding: 0.15em 0.3em;
  border-radius: 5px;
  position: relative;
  white-space: nowrap;
}

span[data-time].highlight {
  background: #ff3333bb !important;  
  transition: background 0.3s ease;
}

span[data-time]::after {
  visibility: hidden;
  opacity: 0;
  content: attr(locale-string);
  position: absolute;
  bottom: 100%;
  left: 0;
  background: var(--lightgray);
  padding: 0.15em 0.3em;
  border-radius: 5px;
  white-space: nowrap;
  overflow-x: visible;
  transition: opacity 0.3s ease;
}

span[data-time]:hover::after {
  opacity: 1;
  visibility: visible;
}

pre.highlight {
  background: var(--dark) !important;
}

pre.highlight > code {
  color: #eee;
  padding: 0;
  background: none !important;
}

.has-focus {
  border-radius: 5px;
  background: var(--lightgray);
}

hr {
  width: 100%;
  margin: 2em 0;
  height: 2px;
  border-radius: 1px;
  border-width: 0;
  color: var(--outlinegray);
  background-color: var(--outlinegray);
}

.overlay {
  position: absolute;
  color: var(--dark);
  width: 280px;
  text-align: center;
  font-size: 1rem;
  z-index: 999;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0.9;
  pointer-events: none;
  transition: opacity 0.4s ease;
}




.visualization {
  position: relative;
}

.visualization:hover > .overlay {
  opacity: 0.1;
} 

